<template>
    <div style="height:100%;min-height:16rem;">
        <div class="top">
            <p>XX的红包</p>
            <span class="talkText">哈哈</span>
        </div>
        <p class="notice">您不能领取未关联馆主的红包，赶紧去关联吧</p>
        <div class="totalPerson">
            <span>领取人</span>
            <span>红包0/5</span>
        </div>
        <div class="button">
            <span class="playGame">前往游戏</span>
        </div>

    </div>
</template>

<script>
export default {
}
</script>

<style lang="less" scoped>
    .top{
        // margin:0 auto;
        font-size: .8rem;
        text-align: center;
        width:100%;
        height:4rem;
        padding-top:20px;
        background: url(../../images/gift_icon_05.png) no-repeat ;
        background-size:100% 100%;
        .talkText{
            display: inline-block;
            font-size:0.6rem;
            width:60%;
            line-height: 1.5em;
            margin-top:18px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow:ellipsis;
            border:1px solid #874D44;
            border-radius:10px;
            background:#BE7468;
            color:#fff;
        }
    }
    .notice{
        margin:30px auto 0;
        text-align:center;
        width:90%;
        font-size:.45rem;
    }
    .totalPerson{
        text-align: center;
        font-size:.45rem;
    }
    .button{
        text-align: center;
        margin-top:20px;
    }
    .playGame{
        display: inline-block;
        height:1.2rem;
        font-size: .5rem;
        line-height: 1.2rem;
        width:4rem;
        border-radius:30px;
        color:#A27222;
        background: #E4D39F;
        text-align: center;
        margin:0 auto;
    }

</style>



